import axios from 'axios';

import { baseUrl } from './';

import {
    GET_IMAGES
} from './';

export const getImages = () => {
  return dispatch => {
    axios.get(`${baseUrl}/posts`)
      .then(response => {
        dispatch({
          type: GET_IMAGES,
          payload: response.data
        });
      })
      .catch(error => {
        console.log(error);
        return { error: true };
      });
  };
}



