import { combineReducers } from 'redux';
import { reducer as form } from 'redux-form';

import auth from './auth';
import locale from './locale';
import user from './user';
import gallery from './gallery';

export default combineReducers({
  auth: auth,
  user: user,
  locale: locale,
  gallery: gallery,
  form: form
});



