import { AUTH_USER, UNAUTH_USER, AUTH_ERROR, AUTH_UNMOUNT, GET_EMAIL } from 'actions';

const auth = (state = {}, action = {}) => {
  switch (action.type) {
    case AUTH_USER:
      return { ...state, error: '', authenticated: true };
    case UNAUTH_USER:
      return { ...state, authenticated: false };
    case AUTH_ERROR:
      return { ...state, error: action.payload };
    case AUTH_UNMOUNT:
      return { ...state, error: '' };
    case GET_EMAIL:
      return { ...state, email: action.payload };
    default:
      return state;
  }
}

export default auth;



